import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from 'src/app/services/api.service';
import { Login } from 'src/app/models/login.model'
import { SnotifyService } from 'ng-snotify';
import { NgxSpinnerService } from 'ngx-spinner';
import { from } from 'rxjs';
import { HttpErrorResponse } from '@angular/common/http';
import { NgForm, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { SelectItem} from 'primeng/components/common/api';
import { Message} from 'primeng/components/common/api';
import { MessageService} from 'primeng/components/common/messageservice';
declare var $: any; 
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  providers: [MessageService]
})
export class LoginComponent implements OnInit {
  registerForm: FormGroup;
  submitted = false;
  messSuccessSignup:string;
  LoginExam = <Login>{}
  msgs: Message[] = [];

  constructor(
    private router :Router,
    private api :ApiService,
    private snotifyService: SnotifyService,
    private spinner: NgxSpinnerService,
    private messageService: MessageService,
    private formBuilder: FormBuilder
    ) { }

  ngOnInit() {
    if(localStorage.getItem('successSignup')){
      this.msgs.push({severity:'success',  summary:localStorage.getItem('successSignup')}); 
      localStorage.removeItem('successSignup');
    }
    this.spinner.show();
    setTimeout(() => {
      this.spinner.hide();
    }, 2000);

    //check token
    if(localStorage.getItem('token')){
      this.router.navigate(['./'])
    }
    this.registerForm = this.formBuilder.group({
      username: ['', Validators.required],
      password: ['', [Validators.required, Validators.minLength(6)]]
    });
  }
  get f() { return this.registerForm.controls; }


  onSubmit(f:NgForm) {
    this.submitted = true;
    if (this.registerForm.invalid) {
        return;
    }
    console.log(f.value)
      //check login
      this.api.loginExam(f.value).subscribe(
        (res) => {
          this.LoginExam = res as Login;
          console.log(this.LoginExam)
          if(this.LoginExam.status_code == 200){
            localStorage.setItem('token', this.LoginExam.data.accessToken);
            this.router.navigate(['./'])
          }
        },
        (error:HttpErrorResponse) => { this.snotifyService.error(error.error.message);}
      )
  }

}
