import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { ApiService } from 'src/app/services/api.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { signUp } from 'src/app/models/signUp.model'
import { SnotifyService } from 'ng-snotify';
import { from } from 'rxjs';
import { HttpErrorResponse } from '@angular/common/http';
declare var $: any; 
@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.scss']
})
export class SignUpComponent implements OnInit {
  registerForm: FormGroup;
  submitted = false;
  mess=''
  signUpExam = <signUp>{}
  constructor(
    private formBuilder: FormBuilder,
    private router:Router,
    private api :ApiService,
    private spinner: NgxSpinnerService,
    private snotifyService: SnotifyService,
    ) { }

  ngOnInit() {
    this.spinner.show();

    setTimeout(() => {
      this.spinner.hide();
    }, 2000);
    //check token
    if(localStorage.getItem('token')){
      this.router.navigate(['./'])
    }

    this.registerForm = this.formBuilder.group({
      username: ['', Validators.required],
      name: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      password: ['', [Validators.required, Validators.minLength(6)]],
      phone: ['', Validators.required],
    });
  }
  
  get f() { return this.registerForm.controls; }

  onSubmit(f:NgForm) {
    this.submitted = true;
    if (this.registerForm.invalid) {
        return;
    }
    const select = $("#demo").val()
    var dataSignup = f.value
    dataSignup.role = new Array();
    dataSignup.role.push(select);

    this.api.signUpExam(dataSignup).subscribe(
      (res) =>{
        this.signUpExam = res as signUp
        if(this.signUpExam.status_code === 200){
          this.router.navigate(['./auth/login']);
          localStorage.setItem('successSignup',this.signUpExam.message)
        }
      },
      (error:HttpErrorResponse)=> {this.snotifyService.error(error.error.message)}
    )
  }

}
